import { useEffect, useState } from "react"
import { Card, CardContent, CardHeader } from "./Card"
import { Candidate } from "@/lib/definitions"

export const CandidateCard = ({ children,candidateData }: { children?: React.ReactNode,candidateData?:Candidate|null }) => {

    const[currentCandidate, setCurrentCandidate] = useState<Candidate | null>(null)

    useEffect(() => {
        if(candidateData)
        setCurrentCandidate(candidateData);
    })
    
    return (
        <Card className="p-4 space-y-4 max-h-full md:text-base text-sm">
            <h1 className="font-semibold text-xl">{currentCandidate?.firstName} {currentCandidate?.lastName}</h1>
            <div className="flex items-center gap-6">
                <h3 className="font-semibold text-gray-500">{currentCandidate?.designation}</h3>
                <h3 className="font-semibold text-gray-500">{currentCandidate?.companyName}</h3>
            </div>
            <CardContent className="bg-white p-4">
                <div className="grid md:grid-cols-2 grid-cols-1 gap-4">
                    <div className="space-y-2">
                        <p>Preferred Job Type</p>
                        <p className="text-blue-600 font-semibold text-sm">{currentCandidate?.preferredJobType ? currentCandidate.preferredJobType : "Not Mentioned"}</p>
                    </div>
                    <div className="space-y-2">
                        <p>Gender</p>
                        <p className="text-blue-600 font-semibold text-sm">{currentCandidate?.gender}</p>
                    </div>
                    <div className="space-y-2">
                        <p>Notice Period</p>
                        <p className="text-blue-600 font-semibold text-sm">{currentCandidate?.noticePeriod} Days</p>
                    </div>
                    <div className="space-y-2">
                        <p>Marital status</p>
                        <p className="text-blue-600 font-semibold text-sm">{currentCandidate?.maritalStatus}</p>
                    </div>
                    <div className="space-y-2">
                        <p>Location</p>
                        <p className="text-blue-600 font-semibold text-sm">{currentCandidate?.currentLocation.locationDetails}</p>
                    </div>
                    <div className="space-y-2">
                        <p>Education</p>
                        <p className="text-blue-600 font-semibold text-sm">{currentCandidate?.highestEducation}</p>
                    </div>
                    <div className="space-y-2">
                        <p>Experience</p>
                        <p className="text-blue-600 font-semibold text-sm">{currentCandidate?.totalExperience} YRS</p>
                    </div>
                    <div className="space-y-2">
                        <p>Expected Salary</p>
                        <p className="text-blue-600 font-semibold text-sm">{currentCandidate?.currentSalary}</p>
                    </div>
                </div>
                <div>
                {children}
                </div>
                
            </CardContent>
        </Card>
    )
}