import { Card, CardContent, CardHeader } from "./Card";
import { Job } from "@/lib/models/client";
import Link from "next/link";
import { useEffect, useState } from "react";
import { Popup } from "./popup";

export function JobCard({
  jobData,
  isClient,
  onClient,
}: {
  jobData: Job;
  isClient: boolean;
  onClient?: boolean;
}) {
  const [currentJob, setCurrentJob] = useState(jobData);
  useEffect(() => {
    setCurrentJob(jobData);
  });

  const [jobUpdated, setJobUpdated] = useState(false);

  const currentDate = new Date();
  const day = String(currentDate.getDate()).padStart(2, "0"); // Ensure 2 digits
  const month = String(currentDate.getMonth() + 1).padStart(2, "0"); // Months are 0-indexed
  const year = currentDate.getFullYear();

  const formattedDate = `${day}/${month}/${year}`;

  return (
    <section className="space-y-4">
      <Card className="border shadow-sm p-4">
        <CardHeader>
          <h3 className="text-xl font-semibold">{currentJob?.techRole}</h3>
        </CardHeader>
        {!isClient && currentJob?.companyName ? (
          <p className="text-lg text-blue-500 font-semibold">
            {currentJob?.companyName}
          </p>
        ) : (
          ""
        )}
        <CardContent className="bg-white  mt-4">
          <div className="grid grid-rows-2 grid-cols-3 gap-6 p-4">
            <div>
              <h4 className="font-semibold text-sm mb-1">Experience</h4>
              <p className="text-gray-600">{currentJob?.experience}</p>
            </div>
            <div>
              <h4 className="font-semibold text-sm mb-1">Status</h4>
              <p className="text-gray-600">{currentJob?.status}</p>
            </div>
            <div>
              <h4 className="font-semibold text-sm mb-1">Budget</h4>
              <p className="text-gray-600">{currentJob?.budget}</p>
            </div>
            <div>
              <h4 className="font-semibold text-sm mb-1">Job Code</h4>
              <p className="text-gray-600">{currentJob?.jobCode}</p>
            </div>
            <div>
              <h4 className="font-semibold text-sm mb-1">Posted On</h4>
              <p className="text-gray-600">{formattedDate}</p>
            </div>
            <div>
              <h4 className="font-semibold text-sm mb-1">Inserted By</h4>
              <p className="text-gray-600">{currentJob?.insertedBy}</p>
            </div>
          </div>
          {jobUpdated && (
            <Popup onClose={() => setJobUpdated(false)}>
              <div className="bg-white w-max-3xl p-4 rounded-lg mt-16">
              <form className="space-y-8">
  <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
    <div className="space-y-2">
      <label htmlFor="experience" className="font-medium">
        Experience
      </label>
      <input
        id="experience"
        type="text"
        value={currentJob?.experience}
        onChange={(e) =>
          setCurrentJob({
            ...currentJob,
            experience: e.target.value,
          })
        }
        className="w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500"
      />
    </div>

    <div className="space-y-2">
      <label htmlFor="status" className="font-medium">
        Status
      </label>
      <input
        id="status"
        type="text"
        value={currentJob?.status}
        onChange={(e) =>
          setCurrentJob({
            ...currentJob,
            status: e.target.value,
          })
        }
        className="w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500"
      />
    </div>

    <div className="space-y-2">
      <label htmlFor="budget" className="font-medium">
        Budget
      </label>
      <input
        id="budget"
        type="text"
        value={currentJob?.budget}
        onChange={(e) =>
          setCurrentJob({
            ...currentJob,
            budget: e.target.value,
          })
        }
        className="w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500"
      />
    </div>

    <div className="space-y-2">
      <label htmlFor="jobCode" className="font-medium">
        Job Code
      </label>
      <input
        id="jobCode"
        type="text"
        value={currentJob?.jobCode}
        onChange={(e) =>
          setCurrentJob({
            ...currentJob,
            jobCode: e.target.value,
          })
        }
        className="w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500"
      />
    </div>

    <div className="space-y-2">
      <label htmlFor="postedOn" className="font-medium">
        Posted On
      </label>
      <input
        id="postedOn"
        type="text"
        value={formattedDate}
        onChange={(e) => {
          const date = new Date(e.target.value);
          setCurrentJob({
            ...currentJob,
            postedOn: date,
          });
        }}
        className="w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500"
      />
    </div>

    <div className="space-y-2">
      <label htmlFor="insertedBy" className="font-medium">
        Inserted By
      </label>
      <input
        id="insertedBy"
        type="text"
        value={currentJob?.insertedBy}
        onChange={(e) =>
          setCurrentJob({
            ...currentJob,
            insertedBy: e.target.value,
          })
        }
        className="w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500"
      />
    </div>
  </div>

  <button
    type="submit"
    className="w-full bg-blue-600 text-white py-2 rounded-md hover:bg-blue-700 transition duration-300"
  >
    Update
  </button>
</form>
                
              </div>
            </Popup>
          )}
        </CardContent>
        <div className="flex items-center gap-4 mt-4 justify-end">
          {!onClient ? (
            <Link href={`/jobs/${currentJob?.jobId}`}>
              <button className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2">
                View Info
              </button>
            </Link>
          ) : (
            ""
          )}

          {isClient ? (
            <button className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2" onClick={() => setJobUpdated(true)}>
              Update
            </button>
          ) : (
            ""
          )}
        </div>
      </Card>
    </section>
  );
}
