import { createSlice, PayloadAction } from "@reduxjs/toolkit";

interface SearchQueries {
  techRole?: string|null;
  minExperience?: number|null;
  maxExperience?: number|null;
  currentLocation?: string|null;
  minSalary?: number|null;
  maxSalary?: number|null;
  noticePeriod?: number|null;
  preferredJobType?: string|null;
  highestEducation?: string|null;
  preferredLocation?: string[]|null;
  domain?: string[]|null;
  mustHaveTechnologies?: string[]|null;
  goodToHaveTechnologies?: string[]|null;
  companies?: string[]|null;
}

const initialState: SearchQueries = {
  techRole: null,
  minExperience: 0,
  maxExperience: 0,
  currentLocation: null,
  minSalary: 0,
  maxSalary: 0,
  noticePeriod: 0,
  preferredJobType: null,
  highestEducation: null,
  preferredLocation: null,
  domain: null,
  mustHaveTechnologies: null,
  goodToHaveTechnologies: null,
  companies: null,
}


const searchSlice = createSlice({
  name: "search",
  initialState,
  reducers: {
    setSearchQuery: (state, action: PayloadAction<SearchQueries>) => {
      state = action.payload;
    },
    clearSearchQuery: (state) => {state = initialState},
  },
});

export const { setSearchQuery, clearSearchQuery } = searchSlice.actions;
export default searchSlice.reducer;
