export const searchSchema = yup.object().shape({
  Technologies: yup.array()
    .of(yup.string())
    .min(1, 'At least one technology is required'),
  Companies: yup.array().of(yup.string()),
  techRole: yup.string().min(3, 'Tech role must be at least 3 characters'),
  CurrentLocation: yup.string().min(3, 'Current location must be at least 3 characters'),
  domains: yup.array().of(yup.string().min(3, 'Domain must be at least 3 characters')),
  preferredLocations: yup.array().of(yup.string().min(3, 'Preferred location must be at least 3 characters')),
  qualification: yup.string().min(3, 'Qualification must be at least 3 characters'),
  experience: yup.number()
    .typeError('Experience must be a number')
    .positive('Experience must be a positive number'),
  salary: yup.number()
    .typeError('Salary must be a number')
    .positive('Salary must be a positive number'),
  noticePeriod: yup.number()
    .typeError('Notice Period must be a number')
    .positive('Notice Period must be a positive number'),
});